* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

body {
	margin: 0;
	padding: 0;

	font-family: sans-serif;
}

#root > div {
	position: relative;

	width: 90vw;
	height: 90vh;
	margin: 5vh 5vw;
}

svg {
	position: absolute;

	width: 100%;
	height: 100%;
}

.buttons {
	position: absolute;
	bottom: 0;

	display: -ms-flexbox;

	display: flex;

	width: 100%;
	padding: .5em;

	-ms-flex-align: center;

	    align-items: center;
	-ms-flex-flow: row nowrap;
	    flex-flow: row nowrap;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}

button {
	-webkit-transition: all .25s;
	-o-transition: all .25s;
	transition: all .25s;

	opacity: .5;
}

button:hover {
	opacity: 1;
}

.editing {
	position: absolute;
	right: 0;

	display: -ms-flexbox;

	display: flex;
	overflow: auto;

	width: 30vw;

	-webkit-transition: all .25s;

	-o-transition: all .25s;

	transition: all .25s;

	-ms-flex-flow: column nowrap;

	    flex-flow: column nowrap;
}

fieldset {
	display: -ms-flexbox;
	display: flex;

	margin: .5em .5em 0;

	border: 1px solid;
	background: #fff;

	-ms-flex-flow: column nowrap;

	    flex-flow: column nowrap;
}

button, legend, label, input {
	cursor: pointer;
}

label {
	display: -ms-flexbox;
	display: flex;

	margin-bottom: .5em;

	-ms-flex-flow: row nowrap;

	    flex-flow: row nowrap;
}

label span {
	-ms-flex: 0 1 50%;
	    flex: 0 1 50%;
}

label input {
	-ms-flex: 0 1 50%;
	    flex: 0 1 50%;
}

.boxes {
	display: -ms-flexbox;
	display: flex;
}

.box {
	width: 2em;
	height: 2em;
	margin-right: .5em;

	border: 1px solid;
}

legend {
	padding: .5em;

	border: 1px solid;
	background: #fff;
}

legend.collapse {
	background: #ddd;
}

legend.collapse ~ label {
	display: none;
}



